		},
		//
		closePayPassword() {
			this.showPayPasswordPopup = false
			this.payPw = '';

		},
		//  
		onInput(key) {
			let that = this
			this.payPw = (this.payPw + key).slice(0, 6);
			if (this.payPw.length == 6) {
				
				let option = {
                    pay_password: this.payPw,
					token: this.user_info.token,
					money: this.moneyTemp,
					type_id:this.guess_type
                }
                this.utils.showLoading();
                this.closePayPassword();
                this.http.post(this.http._HOST + 'wave/buy',option).then(result =>{
                    this.utils.info('...',result); 
                    this.utils.dismissLoading(); 
                    if(result.success){
						
						this.utils.showToast('');

						//
						this.getUserInfo('1');
                       
                    }else{
                        this.utils.showAlert(result.message);
                    }
                })
				
			}
		},
		onDelete() {
			this.payPw = this.payPw.slice(0, this.payPw.length - 1);
		},
		/// type 1 2
		guessFun(type){
			
			if(this.user_info.pay_password == ''){
                this.utils.showAlert('');
                return false;
            }

			this.guess_type = type;
			this.showGuessPopup = true;
			
		},
		///
		closeGuessFun(){
			this.showGuessPopup = false;
			this.money = '';
			this.money_item_index = -1;
		},
		//
		moneyInputFun(){
			
			this.utils.info('money1111...',this.money);
			if(this.money_item_index > -1){
				this.money_item_index = -1;
			}
			
			if(this.money != ''){
				
				this.money = parseInt(this.money) + '';

				if(parseInt(this.money) > 400){
					this.money = 400 + '';
				}
				if(parseInt(this.money) > parseInt(this.user_info.dw_money)){
					this.money = parseInt(this.user_info.dw_money) + '';
				}

			}else{
				
				this.money = '0';
			}

			this.utils.info('money2222...',this.money);
			
		},
		//
		moneyItemClick(item,index){
			if(this.money_item_index == index){
				return false;
			}
			if(parseInt(item) > parseInt(this.user_info.dw_money)){
				this.utils.showToast('');
				return false;
			}
			this.money_item_index = index;
			this.money = item;
		},
		//
		confirmGuess(){
			if(this.money == '' || this.money == '0'){
				this.utils.showToast('');
				return false;
			}

			this.moneyTemp = this.money;
			this.closeGuessFun();
			this.showPayPassword();
		}, 
		//btn  flag 1 2
		getBtnFun(index,flag){
			let type = '';
			if(index == 1){
				// type = '5m'
				type = '1min'
			}
			if(index == 2){
				// type = '15m'
				type = '5min'
			}
			if(index == 3){
				// type = '30m'
				type = '15min'
			}
			if(index == 4){
				// type = '1h'
				type = '30min'
			}
			if(this.chartline && flag && flag == '1'){
				this.chartline.showLoading();
			}
			let url = 'http://api.bitkk.com/data/v1/kline?market=btc_usdt&type=' + type + '&size=40';
			//let url = 'https://data.block.cc/api/v1/kline?market=bitfinex&symbol_pair=BTC_USD&type=' + type +'&limit=50'
			this.$jsonp(url).then(result =>{
				this.utils.info('K...',result);
				
				let btn_data = result.data;
				// for(let i=0;i<btn_data.length;i++){
                //     btn_data[i][0] = this.utils.timeFormat( btn_data[i][0]);
				// }

				let btn_data_tmp = [];
				for(let i=0;i<btn_data.length;i++){
					let item = [];
					item.push(this.utils.timeFormat( btn_data[i][0]));
					item.push(btn_data[i][1]);
					item.push(btn_data[i][4]);
					item.push(btn_data[i][3]);
					item.push(btn_data[i][2]);
					//console.log('item...',item);
					btn_data_tmp.push(item);
				}

				//console.log('hehehehehhehehe...',btn_data_tmp);

				// return false;

				var data0 = this.splitData(btn_data_tmp);

			

				// console.log('data0...',data0);
				// console.log('calculateMA5...',this.calculateMA(data0,5));
				
				if(this.chartline){
					console.log('k...');
					this.chartline.hideLoading();

					// let option = {
					// 	xAxis: {
					// 		data: data0.categoryData,	
					// 	},
					// 	series: [
					// 		{
					// 			data: data0.values,
					// 		},
					// 		{
					// 			data: this.calculateMA(data0,5),
					// 		},
					// 		{
					// 			data: this.calculateMA(data0,10),
					// 		},
					// 		{
					// 			data: this.calculateMA(data0,20),
					// 		},

					// 	]
					// }
					var upColor = '#00da3c';
					var upBorderColor = '#008F28';
					var downColor = '#ec0000';
					var downBorderColor = '#8A0000';

					let option = {
						tooltip: {
							trigger: 'axis',
							axisPointer: {
								type: 'cross'
							}
						},
						legend: {
							data: ['K', 'MA5', 'MA10', 'MA20',],
							selectedMode:false,
							left:0,
							top:0,
							itemGap:10
						},
						grid: {
							left: '40',
							right: '0',
							bottom: '30',
							top:'40'
						},
						xAxis: {
							type: 'category',
							data: data0.categoryData,	
							scale: true,
							boundaryGap : false,
							axisLine: {onZero: false},
							splitLine: {show: false},
							splitNumber: 30,
							min: 'dataMin',
							max: 'dataMax'
						},
						yAxis: {
							scale: true,
							splitArea: {
								show: true
							}
						},
						dataZoom: [
							{
								type: 'inside',
								start: 50,
								end: 100
							},
						],
						series: [
							{
								name: 'K',
								type: 'candlestick',
								data: data0.values,
								itemStyle: {
									normal: {
										color: upColor,
										color0: downColor,
										borderColor: upBorderColor,
										borderColor0: downBorderColor
									}
								},
							
							},
							{
								name: 'MA5',
								type: 'line',
								data: this.calculateMA(data0,5),
								smooth: true,
								lineStyle: {
									normal: {opacity: 0.5}
								}
							},
							{
								name: 'MA10',
								type: 'line',
								data: this.calculateMA(data0,10),
								smooth: true,
								lineStyle: {
									normal: {opacity: 0.5}
								}
							},
							{
								name: 'MA20',
								type: 'line',
								data: this.calculateMA(data0,20),
								smooth: true,
								lineStyle: {
									normal: {opacity: 0.5}
								}
							},

						]
					}
					
					this.chartline.setOption(option);
				}

				
			}).catch(err =>{
				this.utils.info('Kerr...',err);

			})
			
		},
		//k
		splitData(rawData) {
			var categoryData = [];
			var values = []
			for (var i = 0; i < rawData.length; i++) {
				categoryData.push(rawData[i].splice(0, 1)[0]);
				values.push(rawData[i])
			}
			return {
				categoryData: categoryData,
				values: values
			};
		},
		//
		calculateMA(data0,dayCount) {
			var result = [];
			for (var i = 0, len = data0.values.length; i < len; i++) {
				if (i < dayCount) {
					result.push('-');
					continue;
				}
				var sum = 0;
				for (var j = 0; j < dayCount; j++) {
					sum += data0.values[i - j][1];
				}
				result.push((sum / dayCount).toFixed(4));
			}
			return result;
		},
		// type 1 btn 2
		getUserInfo(type){
            let user_id = 'dy' + this.user_info.user_id + this.utils.randomNumber(4);
			this.http.post(this.http._HOST + 'login/user_detail',{user_id:user_id}).then(result =>{
                this.utils.info('...',result);
                if(result.success){
                    let data = result.data;

                    this.user_info.dw_money = data.dw_money;
                    this.user_info.pay_password = data.pay_password;
					this.user_info.token = data.token;
					
					if(type == '1'){
						//btn
						this.getPageInfo();
					}

                }
			})
		},
		//
		getPageInfo(){
			let option = {
                token: this.user_info.token
            }
			this.http.post(this.http._HOST + 'wave/show_btc',option).then(result =>{
				this.utils.info('...',result);
				if(result.success){
					this.page_data = result.data;

					if(this.page_data.risa == 0  && this.page_data.fall == 0){
						this.$refs.homeProgressLeft.style.width = '50%';
					}else{
						this.$refs.homeProgressLeft.style.width = this.page_data.risa + '%';
					}
					

					//
					if(this.timeInterval){
						clearInterval(this.timeInterval);	
					}
					if(this.timeResultInterval){
						clearInterval(this.timeResultInterval);	
					}
					if(this.page_data.user_postbtc){//
						this.page_data.user_postbtc.game_time = parseInt(Math.abs(this.page_data.user_postbtc.game_time)) + 30;
						this.game_time_tmp = parseInt(Math.abs(this.page_data.user_postbtc.game_time)) + 30;

						//
						this.timeResultInterval = setInterval(() =>{
							this.page_data.user_postbtc.game_time --;
							//this.utils.info('...',this.page_data.user_postbtc.game_time);
							if(this.page_data.user_postbtc.game_time == 0){
								this.utils.info('...',);

								//
								this.getDrawResult(this.page_data.user_postbtc.btc_id);
								
								clearInterval(this.timeResultInterval);

								//
								this.getPageInfo();
							}
						},1000)
					}

					//
					this.page_data.next_time = parseInt(Math.abs(this.page_data.next_time)) + 30;
					this.next_time_tmp = parseInt(Math.abs(this.page_data.next_time)) + 30;
					//
					this.timeInterval = setInterval(() =>{
						this.page_data.next_time --;
						//this.utils.info('...',this.page_data.next_time);
						if(this.page_data.next_time == 0){
							this.utils.info('...',);

							clearInterval(this.timeInterval);

							//
							this.getPageInfo();
						}
					},1000)
					
				}
			})
		},
		//
		getDrawResult(btc_id){
			let option = {
				btc_id: btc_id,
				token: this.user_info.token
			}

			this.http.post(this.http._HOST + 'wave/new_result',option).then(result =>{
				this.utils.info('...',result);
				if(result.success){
					
					this.guess_result = result.data;

					this.showGuessResultPopup = true;
				}
			})

		},
		//k
		drawKLine(){
			var upColor = '#ec0000';
            var upBorderColor = '#8A0000';
            var downColor = '#00da3c';
			var downBorderColor = '#008F28';

			let option = {
				tooltip: {
					trigger: 'axis',
					axisPointer: {
						type: 'cross'
					}
				},
				legend: {
					data: ['K', 'MA5', 'MA10', 'MA20',],
					selectedMode:false,
					left:0,
					top:0,
					itemGap:10
				},
				grid: {
					left: '40',
					right: '0',
					bottom: '30',
					top:'40'
				},
				xAxis: {
					type: 'category',
					data: [],
					scale: true,
					boundaryGap : false,
					axisLine: {onZero: false},
					splitLine: {show: false},
					splitNumber: 30,
					min: 'dataMin',
					max: 'dataMax'
				},
				yAxis: {
					scale: true,
					splitArea: {
						show: true
					}
				},
				dataZoom: [
					{
						type: 'inside',
						start: 50,
						end: 100
					},
				],
				series: [
					{
						name: 'K',
						type: 'candlestick',
						data: [],
						itemStyle: {
							normal: {
								color: upColor,
								color0: downColor,
								borderColor: upBorderColor,
								borderColor0: downBorderColor
							}
						},
					
					},
					{
						name: 'MA5',
						type: 'line',
						data: [],
						smooth: true,
						lineStyle: {
							normal: {opacity: 0.5}
						}
					},
					{
						name: 'MA10',
						type: 'line',
						data: [],
						smooth: true,
						lineStyle: {
							normal: {opacity: 0.5}
						}
					},
					{
						name: 'MA20',
						type: 'line',
						data: [],
						smooth: true,
						lineStyle: {
							normal: {opacity: 0.5}
						}
					},

				]
			}
			this.chartline = this.$echarts.init(this.$refs.homeChartContent);
			this.chartline.setOption(option);
		},

		//k
		kTypeChange(index){
			if(this.k_type == index){
				return false;
			}
			this.k_type = index;
			if(this.chartline){
				this.chartline.clear();
			}
			this.getBtnFun(this.k_type,1);
		},
		//
		getDefaultImg(event){
			let default_user_img = require('../assets/imgs/default-user.png');
			event.target.src = default_user_img;
		},
		//
		closeGuessResult(){
			this.showGuessResultPopup = false;
		},

		
	},
	beforeRouteEnter(to,from,next){
		console.log('to...',to);
		console.log('from...',from);
		next();
	},
	beforeDestroy(){
		this.utils.warn('......');

		if(this.k_interval){
			clearInterval(this.k_interval);
		}
		if(this.timeInterval){
			clearInterval(this.timeInterval);
		}
		if(this.timeResultInterval){
			clearInterval(this.timeResultInterval);
		}
	}
}
</script>


